/* ------------------------------------------- Navigation Bar ------------------------------------------- */
.navigationBar
{
    position: absolute;
    width: 100%;
    height: 90px;

    display: flex;
    justify-content: center;
    align-items: center;

    z-index: 1;
}


/* ------------------------------------------- Navigation Bar Inner Containers ------------------------------------------- */


.mainNavItem
{
    width: 33%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}

.navSpacing
{
    width: 35%;
    height: 100%;
}

.otherNavItems
{
    width: 36%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}


/* ------------------------------------------- Navigation Items ------------------------------------------- */


.navItems:link,
.navItems:visited
{
    text-decoration: none;
    color: #fff;
    font-family: "Lato", sans-serif;
    font-weight: bold;
}

.mainLogo
{
    font-size: 40px;
}

.normalItems
{
    font-size: 20px;
    margin: 30px;
}

.normalItems:hover
{
    transform: translateY(-3px);
}

.normalItems:active
{
    transform: translateY(-1px);
}


/* ------------------------------------------- Home Section Containers ------------------------------------------- */


.homeSectionOuterContainer
{
    position: relative;
    width: 100%;
    height: 100vh;
    background-image: radial-gradient(circle, rgba(7,22,48,1) 0%, rgba(11,11,22,1) 100%);

    display: flex;
    justify-content: center;
    align-items: center;
}

.homeSectionInnerContainer
{
    width: 80%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}


/* ------------------------------------------- Home Section Left Side Container ------------------------------------------- */


.homeTextAndButton
{
    width: 60%;
    height: 300px;

    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    margin-right: 50px;;
}


/* ------------------------------------------- Greeting Text ------------------------------------------- */


.greetingText
{
    color: #fff;
    font-family: "Lato", sans-serif;
    font-weight: bold;
    font-size: 55px;

    animation-name: moveLeft;
    animation-duration: 1s;
    animation-timing-function: ease-out;

    margin: 0;
    z-index: 2;
}


/* ------------------------------------------- Description Text ------------------------------------------- */


.descriptionText
{
    width: 650px;
    color: #fff;
    font-family: "Lato", sans-serif;
    font-weight: normal;
    font-size: 25px;

    animation-name: moveLeft;
    animation-duration: 1s;
    animation-timing-function: ease-out;

    z-index: 2;
}


/* ------------------------------------------- Contact Button ------------------------------------------- */


.contactButton
{
    position: relative;
    width: 170px;
    height: 60px;

    border-radius: 100px;
    border: none;

    animation: moveUpAndShow;
    animation-duration: 0.5s;
    animation-timing-function: ease-in;

    background-color: rgb(67, 107, 175);
    z-index: 2;
}

.contactButton:hover
{
    cursor: pointer;

    transform: translateY(-2px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.3);
}

.contactButton:active
{
    transform: translateY(-1px);
    box-shadow: 0 5px 10px rgba(0 ,0, 0, 0.2);
}

.contactButtonText:link,
.contactButtonText:visited
{
    text-decoration: none;
    color: #ffffff;
    font-family: "Lato", sans-serif;
    font-weight: bold;
    font-size: 25px;
}


/* ------------------------------------------- Home Section Right Side Container ------------------------------------------- */


.homeImageContainer
{
    width: 30%;

    display: flex;
    justify-content: center;
    align-items: center;

    margin-left: 50px;
}


/* ------------------------------------------- Home Section Image ------------------------------------------- */


.profilePicture
{
    width: 350px;
    height: 350px;
    border-radius: 100%;
    
    animation-name: moveUpAndDown;
    animation-duration: 4s;
    animation-iteration-count: infinite;
    animation-timing-function: ease;
    transition: all .3s;
}


/* ------------------------------------------- Home Section White Circle Effect ------------------------------------------- */


.whiteCircle1
{
    position: absolute;
    width: 600px;
    height: 600px;

    top: 100px;
    left: 150px;

    background: radial-gradient(circle, rgba(133,182,255,1) 0%, rgba(7,22,48,0) 100%);
    filter: blur(200px);
    border-radius: 100%;

    z-index: 1;
}


/* ------------------------------------------- About Section Outer Container ------------------------------------------- */


.aboutSectionOuterContainer
{
    position: relative;
    width: 100%;
    height: 700px;

    background-image: radial-gradient(circle, rgba(7,22,48,1) 0%, rgba(11,11,22,1) 100%);

    display: flex;
    justify-content: center;
    align-items: center;
}


/* ------------------------------------------- About Section Inner Container ------------------------------------------- */


.aboutSectionInnerContainer
{
    position: relative;
    width: 78%;
    height: 90%;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;

    background-color: #01000e;
    z-index: 2;
}


/* ------------------------------------------- About Section Left Container ------------------------------------------- */


.aboutSectionLeftSide
{
    position: relative;
    width: 50%;
    height: 100%;

    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}


/* ------------------------------------------- About Section Title ------------------------------------------- */


.aboutSectionMainTitle
{
    position: absolute;
    left: 50px;
    color: #ffffff;
    font-family: "Lato", sans-serif;
    font-weight: bold;
    font-size: 40px;

    margin-bottom: 50px;
}


/* ------------------------------------------- About Section Image ------------------------------------------- */


.aboutImage
{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

