    margin: 0;
}


/* ------------------------------------------- About Items Description ------------------------------------------- */


.aboutItemDescription
{
    color: #fff;
    font-weight: normal;
    font-size: 18px;
}


/* ------------------------------------------- Project Section Outer Container ------------------------------------------- */


.projectSectionOuterContainer
{
    width: 100%;
    height: 700px;

    display: flex;
    justify-content: center;
    align-items: center;

    background-image: radial-gradient(circle, rgba(7,22,48,1) 0%, rgba(11,11,22,1) 100%);
}


/* ------------------------------------------- Project Section Inner Container ------------------------------------------- */


.projectSectionInnerContainer
{
