    top: 0px;
    left: 0px;

    color: #ffffff;
    font-family: "Lato", sans-serif;
    font-weight: bold;
    font-size: 40px;
}


/* ------------------------------------------- Project Section Item Image on Right ------------------------------------------- */


.projectItemImageOnRight
{
