}


/* ------------------------------------------- Project Item Left Side Container ------------------------------------------- */


.projectItemLeftSide
{
    width: 60%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    border-radius: 20px 0px 0px 20px;
}


/* ------------------------------------------- Project Item Title ------------------------------------------- */


.projectItemTitle
{
    color: #ffffff;
    font-family: "Lato", sans-serif;
    font-weight: bold;
    font-size: 35px;

    margin-top: 0px;
    margin-left: 30px;
}


/* ------------------------------------------- Project Item Description ------------------------------------------- */


.projectItemDescription
{
    width: 90%;

    color: #fff;
    font-weight: normal;
    font-size: 18px;
    
    margin-top: 0;

    margin-left: 30px;
}


/* ------------------------------------------- Project Item Button Link ------------------------------------------- */


.projectItemButtonLink
{
