    margin-left: 30px;
}


/* ------------------------------------------- Project Item Button ------------------------------------------- */


.projectItemButton
{
    position: relative;
    width: 170px;
    height: 50px;

    border-radius: 100px;
    border: 2px solid rgb(67, 107, 175);

    background-color: transparent;

    text-decoration: none;
    color: #ffffff;
    font-family: "Lato", sans-serif;
    font-weight: bold;
    font-size: 20px;

    transition: all 0.3s;
    overflow: hidden;
    z-index: 1;
}

.projectItemButton:hover
{
    cursor: pointer;
    background-color: rgb(67, 107, 175);
}


/* ------------------------------------------- Project Item Right Side Container ------------------------------------------- */


.projectItemRightSide
{
    width: 40%;
