/* ------------------------------------------- Footer ------------------------------------------- */


footer
{
    position: relative;
    width: 100%;
    height: 100px;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


/* ------------------------------------------- Footer Title Container ------------------------------------------- */


.footerTitleContainer
{
    width: 100%;
    height: 50%;

    display: flex;
    justify-content: center;
    align-items: center;
